



































const VueScrollTo = require('vue-scrollto');
const Youth1 = require('../assets/images/youth-indicators.png');
const Youth2 = require('../assets/images/youth-indicators-2.png');
const APIHighways1 = require('../assets/images/api-highways.png');
const APIHiwghways2 = require('../assets/images/api-highways-2.png');
const Burns = require('../assets/images/burnsmcd.png');
const OneNorth = require('../assets/images/onenorth.png');
import { Component, Vue } from 'vue-property-decorator';
import SlideComponent from '@/components/Slide.vue'; // @ is an alias to /src
import {Slide} from '@/models/slide'
@Component({
  components: {
    SlideComponent
  },
  metaInfo() {
       return {
          title: "Emmanuel Nelson | Home"
      }
   }
})
export default class Home extends Vue {  
  loaded = false;
  compressed = false;
  prevScrollTop = 0;
  cancelScroll = false;
  options = {
    container: '.right-panel',
    offset: -80
  }
  scrollLinks = [
    {
      id: "#about",
      active: true,
      collapse: true
    },
    {
      id: "#youth-indicators",
      active: false,
      collapse: false
    },
    {
      id: "#united-nations",
      active: false,
      collapse: false
    },
    {
      id: "#one-north",
      active: false,
      collapse: false
    },
    {
      id: "#contact",
      active: false,
      collapse: false
    },
  ]
  slides : Slide[] = [{
    id: "youth-indicators",
    title: "U.N. Youth Indicators Application",
    subTitle: "Made in accordance with United Nations OSGEY",
    imageOne: Youth1,
    imageTwo: Youth2,
    role: "Architect, Designer, Developer",
    link: "https://youthindicators.herokuapp.com",
    tools: ["Vuejs/Nuxtjs", "UN Stats API", "MongoDB backend"],
    description: "Entire project designed and developed by myself with guidance from UN OSGEY"
  }, {
    id: "united-nations",
    title: "UN Foundation - APIHighways",
    subTitle: "Made in accordance with Global Partnership for Sustainable Development Data",
    imageOne: APIHighways1,
    imageTwo: APIHiwghways2,
    role: "Developer",
    link: "https://apihighways",
    tools: ["Vuejs", "Microservice Architecture","Kubernetes", "MongoDB backend"],
    description: "Project originally started by Vizzuality, I took over as a solo developer to add new features and fix bugs"
  },
  {
    id: "one-north",
    title: "One North Interactive Websites",
    subTitle: "Made for One North",
    imageOne: OneNorth,
    imageTwo: Burns,
    links: [{url:"https://onenorth.com", text:"One North"},{url:"https://burnsmcd.com", text:"Burns McDonell"},{url:"https://taftlaw.com", text:"Taft Law"},{url:"https://plantemoran.com", text:"Plante Moran Website"}, {url:"https://torys.com", text:"Torys Website"}],
    role: "Senior Developer",
    tools: ["Vuejs/Angular", "Sitecore", "KeystoneJs", ".NET", "Azure"],
    description: "A sampling of projects developed entirely on my own (design and strategy not my own), other times on a team of developers.  Front-end and Back-end development"
  }]

  mounted() {
    let that = this;
    setTimeout(() => {
      this.loaded = true;
    }, 200)
    //this.slides[this.slides.length - 1].offsetTop = (this.$refs.contact as HTMLElement).offsetTop;
  }
  
  scrollTo(link:any) {
    if(this.cancelScroll)
      return
    this.cancelScroll = true;
    this.compressed = !link.collapse;    
    VueScrollTo.scrollTo(link.id,500,this.options);
    //this.clearActiveScroll();
    // link.active = true;
    let that = this;
    setTimeout(() => {
      this.cancelScroll = false
    },900)
  }
  getActiveScrollNdx() {
    return this.scrollLinks.findIndex(x => x.active)
  }
  clearActiveScroll() {
    this.scrollLinks.forEach(element => {
      element.active = false;
    });
  }
  handleScroll(e:any) {
    // console.log(this.slides)
    let ndx = this.getActiveScrollNdx();
    let activeSlide = this.slides[ndx]; 
    const offset = 100;
    if(ndx != this.scrollLinks.length - 1) {
      if(activeSlide && activeSlide.offsetTop) {
        if(e.target.scrollTop > (activeSlide.offsetTop - offset)) {
          this.clearActiveScroll();
          this.scrollLinks[ndx+1].active = true;
          return;
        }
      } else {
        if(e.target.scrollTop > ((this.$refs.contact as HTMLElement).offsetTop - offset)) {
          this.clearActiveScroll();
          this.scrollLinks[ndx+1].active = true;
          return;
        }
      }
    }
    if(ndx != 0) {
      let prevSlide = this.slides[ndx-1];
      if(prevSlide && prevSlide.offsetTop && (e.target.scrollTop < (prevSlide.offsetTop - offset))) {
        this.clearActiveScroll();
        this.scrollLinks[ndx-1].active = true; 
      }
      else if(!prevSlide) {
        prevSlide = (this.$refs.contact as HTMLElement)
        if(prevSlide && prevSlide.offsetTop && (e.target.scrollTop < (prevSlide.offsetTop - offset))) {
          this.clearActiveScroll();
          this.scrollLinks[ndx-1].active = true; 
        }
      }
    }
    if(!this.compressed && e.target.scrollTop > 40) {
      this.scrollTo(this.scrollLinks[1])
    }
    if(this.compressed && e.target.scrollTop < 40) {
      //this.scrollTo(this.scrollLinks[1])
      this.compressed = false;
    }
  }
  track () {
      this.$ga.page('/')
  }
}
